import { Square } from "./square.js"

class Cell extends Square {
  constructor({ coords, band, stack, x, y }) {
    super({ coords })

    this.band = band
    this.stack = stack
    this.x = x
    this.y = y

    this.draw = null
    this.element = null

    this.label = null
  }

  key() {
    if (!this._key) this._key = `${this.band}-${this.stack}-${this.x}-${this.y}`

    return this._key
  }

  draw(svg) {}

  set el(element) {
    let cell = (this.element = element)

    this.label(`${this.x},${this.y}`)

    cell.on("mouseover", () => {
      cell.fill("#6ac")
    })

    cell.on("mouseout", () => {
      cell.fill("#fff")
    })
  }
}

export { Cell }
