class Square {
  constructor({ coords }) {
    this.coords = coords // geometry
    this.center = {
      x: this.coords[0] + this.coords[2] / 2,
      y: this.coords[1] + this.coords[3] / 2,
    }
  }

  label(str) {
    if (this._label) {
      this._label.text(str)
    } else {
      this._label = this.draw.text(str).css({ "pointer-events": "none" })
    }
    this._label.center(this.center.x, this.center.y)
  }
}

export { Square }
